/*
 * @Descripttion: 基础配置主页面js
 * @version: 0.1
 * @Author: ZJM
 * @Date: 2020-05-21 15:26:48
 * @LastEditors: ZJM
 * @LastEditTime: 2022-07-26 16:42:27
 */
// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 引入table组件
import VXETable from '@/components/common/Table';
// 引入提示组件
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
// 导入日期处理类库
import moment from 'moment';
import {
  STATUS_CODE,
  TITLE,
  VARIABLE,
  BTN_TEXT,
  CONSTANTS_PRODUCT,
  EXPORT_NAME
} from '@/utils/constantsFile';
// 搜索组建
import SearchBox from '@/components/common/SearchBox';
import CustomColumn from '@/components/common/CustomColumn';
// 导入所需接口
import {
  exportExportProductionTaskReportByConditionAction
} from '@/api/export';

import {
  productionTaskGetListProductionTaskQueryParameterAction
} from '@/api/product';
import {
  reportGetPageProductionTaskReportByConditionAction
} from '@/api/productReport';

import { searchCondetionFunc } from '@/utils/publicFunction';

export default {
  name: 'ProductManagement',
  components: {
    // 加载标题组件
    HeaderTitle,
    // 加载标题元素组件
    HeadOperationItem,
    // 加载table组件
    VXETable,
    SearchBox,
    CustomColumn
  },
  data() {
    return {
      EXPORT_NAME: EXPORT_NAME,
      VARIABLE: VARIABLE,
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_PRODUCT: CONSTANTS_PRODUCT,
      // 搜索框有关
      isSearchBoxShow: false,
      // table多选数据
      dataChecks: [],
      searchList: [],
      taskQRcode: '',
      tableName: 'device',
      // 模态框名称
      titlename: '',
      // table加载动画
      tableLoading: false,
      clickRow: {},
      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          field: 'createTime',
          title: CONSTANTS_PRODUCT.CREATE_DATE,
          align: 'center',
          fixed: 'left',
          width: '120',
          formatter({ cellValue, row, column }) {
            if (cellValue) {
              return moment(cellValue).format('YYYY-MM-DD');
            } else {
              return '';
            }
          }
        },
        {
          field: 'groupName',
          title: CONSTANTS_PRODUCT.LINE_NAME,
          align: 'center',
          width: '120'
        },
        {
          field: 'taskCode',
          title: CONSTANTS_PRODUCT.WORK_CODE,
          align: 'center',
          width: '180'
        },
        {
          field: 'materialsCode',
          title: CONSTANTS_PRODUCT.MATERIAL_CODE,
          align: 'center',
          width: '180'
        },
        {
          field: 'materialsName',
          title: CONSTANTS_PRODUCT.MATERIAL_NAME,
          align: 'center',
          width: '120'
        },
        {
          field: 'materialsModel',
          title: CONSTANTS_PRODUCT.MATERIAL_NORMS,
          align: 'center',
          width: '120'
        },
        {
          field: 'modelNumber',
          title: CONSTANTS_PRODUCT.MODULE_NUM,
          width: '120',
          align: 'center'
        },
        {
          field: 'taskCount',
          title: CONSTANTS_PRODUCT.WORK_NUM,
          align: 'center',
          width: '120'
        },
        {
          field: 'completeCount',
          title: CONSTANTS_PRODUCT.FINISH_NUM,
          align: 'center',
          width: '120'
        },
        {
          field: 'inspectCount',
          title: CONSTANTS_PRODUCT.INSPECT_NUM,
          align: 'center',
          width: '120'
        },
        {
          field: 'ngCount',
          title: CONSTANTS_PRODUCT.NG_NUM,
          align: 'center',
          width: '120'
        },
        {
          field: 'achievementRate',
          title: CONSTANTS_PRODUCT.REACH_NUM,
          align: 'center',
          width: '120'
        },
        {
          field: 'taskDeliveryTime',
          title: CONSTANTS_PRODUCT.TASK_DUE_DATE,
          align: 'center',
          width: '120',
          formatter({ cellValue, row, column }) {
            if (cellValue) {
              return moment(cellValue).format('YYYY-MM-DD');
            } else {
              return '';
            }
          }
        },
        {
          field: 'startTime',
          title: CONSTANTS_PRODUCT.START_DATE,
          align: 'center',
          width: '160'
        },
        {
          field: 'endTime',
          title: CONSTANTS_PRODUCT.END_DATE,
          align: 'center',
          width: '160'
        },
        {
          field: 'operatorUserRealName',
          title: CONSTANTS_PRODUCT.OPERATOR,
          align: 'center',
          width: '120'
        },
        {
          field: 'client',
          title: CONSTANTS_PRODUCT.CUSTOMER,
          align: 'center',
          width: '120'
        },
        {
          field: 'remark',
          title: CONSTANTS_PRODUCT.REMARK,
          align: 'center',
          width: '120'
        },
        {
          field: 'taskStateName',
          title: CONSTANTS_PRODUCT.START,
          align: 'center',
          width: '120'
        }
      ],
      // 保存搜索内输入的搜索条件，防止翻页时变回无条件查询
      savedSearchCondition: []
    };
  },
  computed: {},
  created() {
    // 加载扩展字段
    this.setTableColume();
    // 调用设备列表查询
    this.findList();
    this.getSearchList();
  },
  watch: {
  },
  methods: {
    /**
     * @description: table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: ZJM
     */
    onTablePageChange(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePage.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 查询所属参数
     * @param val
     * @return: 无
     * @author: ZJM
     */
    async findList() {
      try {
        // 显示table加载动画
        this.tableLoading = true;

        const params = {
          current: this.tablePage.currentPage,
          size: this.tablePage.pageSize
        };
        var conditionJson = {
          conditionJson1: [],
          operatorGroup: 1,
          conditionJson2: []
        };
        if (this.savedSearchCondition.length > 0) {
          conditionJson = searchCondetionFunc(...this.savedSearchCondition);
        }

        const res = await reportGetPageProductionTaskReportByConditionAction(params, conditionJson);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          this.tableDatas = res.rows;
          this.tablePage.total = res.total;
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 获取查询条件
     * @param 无
     * @return: 无
     * @author: CH
     */
    async getSearchList() {
      try {
        const res = await productionTaskGetListProductionTaskQueryParameterAction({});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // showSuccessMessage(res.message);
          // this.currentChangeRow = {};
          this.searchList = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: （主页面）点击“搜索”按钮，打开搜索框
     * @param 无
     * @return: void
     * @author: cyb
     */
    showSearchCard() {
      // alert(1321);
      this.isSearchBoxShow = !this.isSearchBoxShow;
    },
    /**
     * @description:处理查询数据
     * @param {type}
     * @return:
     * @author: CH
     */
    findTable(conditionArr1, groupOperator, conditionArr2) {
      this.searchData = [];
      if (conditionArr1 !== undefined) {
        this.searchData.conditionArr1 = JSON.parse(
          JSON.stringify(conditionArr1)
        );
        for (var i in this.searchData.conditionArr1) {
          this.searchData.conditionArr1[
            i
          ].value = this.searchData.conditionArr1[i].value.toString();
        }
      }
      if (groupOperator !== undefined) {
        this.searchData.groupOperator = groupOperator;
      }
      if (conditionArr2 !== undefined) {
        this.searchData.conditionArr2 = JSON.parse(
          JSON.stringify(conditionArr2)
        );
        for (var x in this.searchData.conditionArr2) {
          this.searchData.conditionArr2[
            x
          ].value = this.searchData.conditionArr2[x].value.toString();
        }
      }
      this.onTablePageChange(1, this.tablePage.pageSize);
    },
    /**
     * @description: 显示扩展字段列表头
     * @return: void
     * @author: ZJM
     */
    async setTableColume() {
      // 设置恢复时的默认值
      for (var x = 0; x < this.tableColumns.length; x++) {
        this.tableColumns[x].zIndex = x;
        if (this.tableColumns[x].visible === undefined) {
          this.tableColumns[x].yVisible = true;
        } else {
          this.tableColumns[x].yVisible = this.tableColumns[x].visible;
        }
        if (this.tableColumns[x].width === undefined) {
          this.tableColumns[x].yWidth = 'auto';
        } else {
          this.tableColumns[x].yWidth = this.tableColumns[x].width;
        }
      }
    },
    /**
    * @description: 切换显示自定义列模态框
    * @param 无
    * @return: void
    * @author: ZJM
    */
    showCustomColumn() {
      this.dialogVisibleCustomColumn = !this.dialogVisibleCustomColumn;
    },
    /**
    * @description: 保存自定义列
    * @param 无
    * @return: void
    * @author: ZJM
    */
    saveCustomColumn(columns) {
      this.tableColumns = columns;
      this.showCustomColumn();
    },
    /**
    * @description: 恢复默认自定义列
    * @param 无
    * @return: void
    * @author: ZJM
    */
    resetCustomColumn() {
      this.showCustomColumn();
      // 按时间从大到小
      this.tableColumns = this.tableColumns.sort((a, b) => {
        return a.zIndex > b.zIndex ? 1 : -1;
      });
      this.tableColumns = this.tableColumns.map((item) => {
        item.width = item.yWidth;
        item.visible = item.yVisible;
        return item;
      });
    },
    refreshTable() {
      this.findList();
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTable() {
      try {
        var conditionJson = {
          conditionJson1: [],
          operatorGroup: 1,
          conditionJson2: []
        };
        if (this.savedSearchCondition.length > 0) {
          conditionJson = searchCondetionFunc(...this.savedSearchCondition);
        }
        const res = await exportExportProductionTaskReportByConditionAction({}, conditionJson);
        if (res.data.code !== undefined) {
          showWarningMessage(res.message);
        } else {
          this.$options.filters.setExportFunc(res, EXPORT_NAME.TASK_MANAGEMENT + '.xls');
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 刷新表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableData(savedSearchCondition) {
      if (savedSearchCondition) {
        this.savedSearchCondition = [...savedSearchCondition];
        this.tablePage.currentPage = 1;
      }
      this.findList();
    }
  }
};
